.button {
  border-color: rgb(49, 138, 68);
  border-radius: 5px;
  padding: 5px;
  color: #fff;
  font-weight: 700;
  box-shadow: 0 0 10px 10px rgb(49, 138, 68) inset, 0 0 0 0 black;
  -webkit-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
  cursor: pointer;
}
.button:hover,
.button:focus {
  box-shadow: 0 0 5px 0 rgb(49, 138, 68) inset, 0 0 10px 4px rgb(49, 138, 68);
  color: black;
}
